import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

 import Index from '../pages/Index'
 import lilyhana from '../pages/lilyhana'
 import makotojinno from '../pages/makotojinno'
 import yoshiyukisuganuma from '../pages/yoshiyukisuganuma'
 import clinic from '../pages/clinic'
 import story from '../pages/story'
 import myport from '../pages/myport'
 import corp from '../pages/corp'
 import spa from '../pages/spa'
 

export default new VueRouter({
mode: 'history',
routes: [
     { path: '/', component: Index },
     { path: '/lilyhana', component: lilyhana },
     { path: '/makotojinno', component: makotojinno  },
     { path: '/yoshiyukisuganuma', component:yoshiyukisuganuma  },
     { path: '/clinic', component:clinic  },
     { path: '/story', component:story  },
     { path: '/myport', component:myport  },
     { path: '/corp', component: corp },
     { path: '/spa', component:spa  },
     { path: '*', component: Index }
     
]




})

